import React, { FC, useEffect } from "react";
import { useRouteMatch } from "react-router-dom";
import { Dispatch } from "redux";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Icon from "@material-ui/core/Icon";
import {
  fetchSingleOrder,
  fetchUpdateOrderStatus,
} from "../../redux/orders/actions";
import { useForm } from "react-hook-form";
import { RootState } from "../../redux/RootReducer";
import moment from "moment";
import "./order-item.scss";
import { FormSelect } from "../../components/FormSelect/FormSelect";
import "../ProductsList/products-list.scss";
import { differenceFormat, dateToLisbonTimezone, parseISODate } from "../../common/utils/date"
import axios from 'axios'

type EditOrder = {
  status: string;
};

interface OrderItemProps {
  singleOrder: any;
  user: any;
  fetchSingleOrder: (orderId: string) => void;
  fetchUpdateOrderStatus: (orderId: string, newStatus: string, userId: string) => void;
}

export const STATUS_NAMES: Record<string, string> = {
  "WAIT FOR CARD PAYMENT": 'Wait for card payment',
  "DELIVERING": "Ready to pick",
  "ACCEPTED": "Picking",
  "SENT": "Delivering",
  "CLOSE": "Delivered",
  "CANCELED": "Canceled",
}

interface InfoBlockProps {
  title: string
  value: any
  visible?: boolean
}

const InfoBlock = ({ title, value, visible = true }: InfoBlockProps) => {
  if (!visible) {
    return null
  }

  return (
    <div className="Order-Item-Column">
      <p className="Order-Item-Form-Title">{title}</p>
      <p className="Order-Item-Form-Text">{value}</p>
    </div>
  )
}

const OrderItemContainer: FC<OrderItemProps> = ({
  fetchSingleOrder,
  fetchUpdateOrderStatus,
  singleOrder,
  user,
}) => {
  const match = useRouteMatch<any>();
  const { register, handleSubmit, errors, setValue, control } = useForm();
  const { orderId } = match.params;

  console.log(singleOrder);

  useEffect(() => {
    fetchSingleOrder(orderId);
  }, []);

  const getDifferencePick = (): string | null => {
    if (!singleOrder?.dateOfAccepted) {
      return null
    }

    const dateOfAccepted = moment(singleOrder.dateOfAccepted)
    const date = moment(singleOrder.date)
    console.log(dateOfAccepted.toDate())
    console.log(date.toDate())

    return differenceFormat(
      parseISODate(singleOrder.dateOfAccepted),
      parseISODate(singleOrder.date),
    )
  }

  const getDifferenceClose = (): string | null => {
    if (!singleOrder?.dateOfClose) {
      return null
    }

    return differenceFormat(
      parseISODate(singleOrder.dateOfClose),
      parseISODate(singleOrder.date),
    )
  }

  const checkIsButtonStatusDisabled = () => {
    if (!singleOrder?.status) {
      return true
    }

    if (user.type === 'picker') {
      return (
        singleOrder.status === 'SENT' ||
        singleOrder.status === 'CLOSE' ||
        singleOrder.status === 'CANCELED'
      )
    }

    if (user.type === 'dispatcher') {
      return (
        singleOrder.status === 'CLOSE' ||
        singleOrder.status === 'CANCELED'
      )
    }

    return true
  } 

  const disabledStatus = checkIsButtonStatusDisabled()

  useEffect(() => {
    if (singleOrder)
      setValue("status", {
        value: singleOrder.status,
        label: STATUS_NAMES[singleOrder.status],
      });
  }, [singleOrder]);

  const onSubmit = (data: EditOrder) => {
    console.log(data);
  };

  const onClickShowMaps = async () => {
    const address = singleOrder.address.streetName.replace(" ", "+");
    const url = `https://www.google.com.ua/maps/place/${address}+${singleOrder.address.streetNumber}+Lisboa`;

    window.open(url);
  };

  const onChangeStatusByRole = () => {
    if (user.type === 'picker') {
      fetchUpdateOrderStatus(singleOrder._id, 'SENT', singleOrder.userId)
    }

    if (user.type === 'dispatcher') {
      fetchUpdateOrderStatus(singleOrder._id, 'CLOSE', singleOrder.userId)
    }
  }

  const getTotalOrderPrice = () => {
    if (singleOrder.promocode && singleOrder.promocode.discount) {
      if (!singleOrder.promocode.type || singleOrder.promocode.type === 'CASH') {
        const total = singleOrder.price + singleOrder.promocode.discount ?? 0
        return `${total.toFixed(2)} €`
      }

      const percent = singleOrder.price * 100 / (100 - singleOrder.promocode.discount)
      return `${percent.toFixed(2)} €`
    }

    return `${singleOrder.price.toFixed(2)} €`;
  };

  const priorityCompare = (a: any, b: any) => {
    const aPriority = a.priority || 0;
    const bPriority = b.priority || 0;

    if (aPriority < bPriority) return -1;
    if (aPriority > bPriority) return 1;

    return 0;
  };

  if (!singleOrder) {
    return null
  }

  return (
    <div className="Order-Item">
      <div className="Order-Item-Header">
        <p className="Order-Item-Header-Title" style={{ display: 'flex', flexDirection: 'row' }}>Order{'  '}
          {singleOrder.paymentType === 'CARD' && <p style={{ color: 'green', marginLeft: 5 }} >PAID</p>}
        </p>
        <div className="Order-Item-Header-Button">
          {singleOrder?.dateOfAccepted && (
            <div style={{ fontWeight: 'bold' }} > 
              Time of picking - {getDifferencePick()}
            </div>
          )}

          {singleOrder?.dateOfClose && (
            <div style={{ fontWeight: 'bold' }} > 
              Time of close - {getDifferenceClose()}
            </div>
          )}
        </div>
      </div>
      <form className="Order-Item-Form" onSubmit={handleSubmit(onSubmit)}>
        <div className="Order-Item-Row">
          <InfoBlock title="Date" value={moment(singleOrder.date).format("DD/MM/YY HH:mm")} />
          <InfoBlock title="Order number" value={singleOrder.orderNumber} />

          <div className="Order-Item-Column">
            <div style={{ width: '100%' }}>
              {user.type === "admin" || user.type === "picker" ? (
                <div>
                  <p className="Order-Item-Form-Title">Status</p>
                  <FormSelect
                    placeholder={"Status"}
                    name={"status"}
                    options={[
                      { _id: "DELIVERING", name: "Ready to pick" },
                      { _id: "ACCEPTED", name: "Picking" },
                      { _id: "SENT", name: "Delivering" },
                      { _id: "CLOSE", name: "Delivered" },
                      { _id: "CANCELED", name: "Canceled" },
                    ].map((el: any) => {
                      return { value: el._id, label: el.name };
                    })}
                    errors={errors}
                    control={control}
                    onChangeAction={(value: string) =>
                      fetchUpdateOrderStatus(singleOrder._id, value, singleOrder.userId)
                    }
                    required={{ required: true }}
                  />
                </div>
              ) : (
                <div>
                  <p className="Order-Item-Form-Title">Status</p>
                  <p className="Order-Item-Form-Text">{STATUS_NAMES[singleOrder.status]}</p>
                </div>
              )}

              {(user.type === 'picker' || user.type === "dispatcher") && (
                <div
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    alignItems: "center",
                    width: '100%',
                    opacity: disabledStatus ? 0.4 : 1,
                    cursor: !disabledStatus ? "pointer" : undefined,
                  }}
                  onClick={!disabledStatus ? onChangeStatusByRole : () => {}}
                >
                  <div
                    style={{
                      border: "2px solid #c700cb",
                      borderRadius: 10,
                      padding: 7,
                      width: '100%',
                      color: "#c700cb",
                    }}
                  >
                    {user.type === 'picker' && 'Picked'}
                    {user.type === 'dispatcher' && 'Delivered'}
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>

        <div className="Order-Item-Row">
            <InfoBlock title="Payment type" value={singleOrder.paymentType} />
            <InfoBlock title="Subtotal" value={getTotalOrderPrice()} />
            <InfoBlock title="Total" value={`${singleOrder.price?.toFixed(2)} €`} />
            <InfoBlock 
              title="Promocode" 
              value={singleOrder.promocode?.code 
                      ? user.type === "admin" 
                          ? singleOrder.promocode.code 
                          : "Used"
                      : "None"}
            />
        </div>

          <div className="Order-Item-Row">
            <InfoBlock title="NIF" value={singleOrder?.nif} visible={!!singleOrder?.nif}/>
            <InfoBlock title="User Name" value={singleOrder.userName} visible={!!singleOrder.userName} />
            <InfoBlock title="Phone Number" value={singleOrder.phoneNumber} visible={!!singleOrder.phoneNumber} />
            <InfoBlock title="Comment" 
              value={singleOrder.address?.comment
                ? singleOrder.address.comment
                : "Not added"}
            />
          </div>

          <p className="Order-Item-Form-Main" style={{ marginBottom: 30, marginTop: 50 }}>ADDRESS</p>

          <div className="Order-Item-Row">
            <InfoBlock title="Street Name" 
              value={singleOrder.address?.streetName
                ? singleOrder.address.streetName
                : "Not added"}
            />

            <InfoBlock title="Street Number" 
              value={singleOrder.address?.streetNumber
                ? singleOrder.address.streetNumber
                : "Not added"}
            />

            <InfoBlock title="Floor" 
              value={singleOrder.address?.floor
                ? singleOrder.address.floor
                : "Not added"}
            />
          </div>

          <div
            style={{
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
              marginBottom: 25,
              cursor: "pointer",
            }}
            onClick={onClickShowMaps}
          >
            <div
              style={{
                border: "2px solid #c700cb",
                borderRadius: 10,
                padding: 7,
                color: "#c700cb",
              }}
            >
              Show on maps
            </div>
          </div>
          <p className="Order-Item-Form-Main">Products</p>
          <table className="Orders-Table">
            <thead>
              <tr>
                <th>Image</th>
                <th>Title</th>
                <th>Storage Info</th>
                <th>Count</th>
                <th>Price</th>
                <th>Priority</th>
              </tr>
            </thead>
            <tbody>
              {singleOrder.products
                ? singleOrder.products
                    .sort(priorityCompare)
                    .map((item: any, index: number) =>
                      !item.price ? null : (
                        <>
                          <tr>
                            <td align="left">
                              <img
                                src={item.images[0]}
                                alt=""
                                className="Orders-Table-Image"
                              />
                            </td>
                            <td className="Orders-Table-Title" align="left">
                              {item.title_pt}
                            </td>
                            <td className="Orders-Table-Title" align="left">
                              {item.storage_info}
                            </td>
                            <td className="Orders-Table-Title" align="left">
                              {item.count}
                            </td>
                            <td className="Orders-Table-Title" align="left">
                              {item.price.current.toFixed(2)}
                            </td>
                            <td className="Orders-Table-Title" align="left">
                              {item.priority || 0}
                            </td>
                          </tr>
                        </>
                      )
                    )
                : null}
            </tbody>
          </table>
      </form>
    </div>
  );
};

const mapStateToProps = (state: RootState) => {
  return {
    singleOrder: state.orders.singleOrder,
    user: state.user.user,
  };
};

const mapDispatchToProps = (dispatch: Dispatch) => ({
  fetchSingleOrder: (orderId: string) => dispatch(fetchSingleOrder(orderId)),
  fetchUpdateOrderStatus: (orderId: string, newStatus: string, userId: string) =>
    dispatch(fetchUpdateOrderStatus(orderId, newStatus, userId)),
});

export default connect(mapStateToProps, mapDispatchToProps)(OrderItemContainer);
