import {
  format,
  parseISO,
  isAfter,
  isBefore,
  subDays,
  isDate as isDateFNS,
  parse,
  differenceInMinutes,
  differenceInSeconds,
} from "date-fns";
import { utcToZonedTime } from "date-fns-tz";
import moment from "moment";

export const parseISODate = (date: Date | string) =>
  typeof date === "string" ? parseISO(date) : date;

export const isDate = (date: Date | string) => isDateFNS(date);

export const subDateDays = (date: Date | string, amount: number) =>
  subDays(parseISODate(date), amount);

export const isDateBefore = (
  date: Date | string,
  dateToCompare: Date | string
) => isBefore(parseISODate(date), parseISODate(dateToCompare));

export const isDateAfter = (
  date: Date | string,
  dateToCompare: Date | string
) => isAfter(parseISODate(date), parseISODate(dateToCompare));

export const monthFormatter = (date: Date | string, divider = ".") =>
  format(parseISODate(date), `yy${divider}MM${divider}dd`);

export const monthFormatterFullYear = (date: Date | string, divider = ".") =>
  format(parseISODate(date), `yyyy${divider}MM${divider}dd`);

export const fullDateFormatter = (date: Date | string, divider = ".") =>
  format(parseISODate(date), `HH:mm aaa iiii do LLLL`);

export const dayDateFormatter = (date: Date | string, divider = ".") =>
  format(parseISODate(date), `iiii do LLLL`);

export const timeFormatter = (date: Date | string) =>
  format(parseISODate(date), "HH:mm");

export const parseShortDate = (date: string) =>
  parse(date, "dd/MM/yyyy", new Date());

export const now = () =>
  parse(format(new Date(), "yyyy-MM-dd"), "yyyy-MM-dd", new Date());

export const dateToLisbonTimezone = (date: Date) => {
  return utcToZonedTime(date, 'Asia/Tokyo')
}

export const differenceFormat = (distanceDate: Date, endDate: Date) => {
  const seconds = differenceInSeconds(
    dateToLisbonTimezone(distanceDate), 
    dateToLisbonTimezone(endDate)
  );

  return secondsToTimer(seconds)
}

export const secondsToTimer = (seconds: number) => {
  const hour = Math.floor(seconds / 3600);
  const minute = Math.floor(seconds / 60) % 60;
  const second = seconds % 60;

  return [hour, minute, second]
    .map(time => time < 10 ? "0" + time : time)
    .filter((time, index) => time !== "00" || index > 0)
    .join(":");
}